    background-color: #2c3531;
    flex: 1;
}

.Dashboard .add-margin-top {
    margin-top: 56px;
}

.container-fluid {
    padding: 15px;
}

@media (min-width: 1200px) {
    .row .category-col:nth-child(5n + 1) {
        -webkit-box-flex: 0;
        flex: 0 0 50%;
        max-width: 50%;
    }

    .row .category-col:nth-child(5n + 2) {
        -webkit-box-flex: 0;
        flex: 0 0 50%;
        max-width: 50%;
    }
}
