.AppHeader {
    box-shadow: 0 -4px 10px #000;
}

.AppHeader.show-bg {
    background-color: #fff;
    -webkit-transition: background-color 500ms ease;
    transition: background-color 500ms ease;
}

.AppHeader.show-bg .nav-link {
    color: #000 !important;
    -webkit-transition: color 500ms ease;
    transition: color 500ms ease;
}

.AppHeader.hide-bg {
    background-color: unset;
    -webkit-transition: background-color 500ms ease;
    transition: background-color 500ms ease;
}

.AppHeader.hide-bg .nav-link {
    color: #fff !important;
    -webkit-transition: color 500ms ease;
    transition: color 500ms ease;

}

.AppHeader .navbar-brand {
    font-weight: 600;
}

.AppHeader .nav-link {
    font-weight: 600;
}

.AppHeader .nav-link:hover {
    opacity: 0.7;
}

.AppHeader.show-bg .selected-nav-link {
    border-bottom: 2px solid #000;
    -webkit-transition: border-color 500ms ease;
    transition: border-color 500ms ease;
}

.AppHeader.hide-bg .selected-nav-link {
    border-bottom: 2px solid #fff;
    -webkit-transition: border-color 500ms ease;
    transition: border-color 500ms ease;
}

.AppFooter {
    background-color: #2c3531;
}

.Dashboard {
    /*padding-top: 56px;*/
    background-color: #2c3531;
    flex: 1 1;
}

.Dashboard .add-margin-top {
    margin-top: 56px;
}

.container-fluid {
    padding: 15px;
}

@media (min-width: 1200px) {
    .row .category-col:nth-child(5n + 1) {
        flex: 0 0 50%;
        max-width: 50%;
    }

    .row .category-col:nth-child(5n + 2) {
        flex: 0 0 50%;
        max-width: 50%;
    }
}

.LandingPageImage {
    height: 60vh;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.2)), to(rgba(44,53,49,0.7))), url(/chuck-norris-react/static/media/chuckNorrisBackground.c014f241.jpg);
    background-image: -webkit-linear-gradient(top, rgba(255,255,255,0.2) 0%, rgba(44,53,49,0.7) 100%), url(/chuck-norris-react/static/media/chuckNorrisBackground.c014f241.jpg);
    background-image: linear-gradient(to bottom, rgba(255,255,255,0.2) 0%, rgba(44,53,49,0.7) 100%), url(/chuck-norris-react/static/media/chuckNorrisBackground.c014f241.jpg);
    background-size: cover;
    width: 100%;
}

.LandingPageImage .text-shadow {
    text-shadow: 2px 2px 4px #000;
}

.Fact {
    text-shadow: 2px 2px 4px #000;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1
}

.Fact:hover {
    opacity: 0.7;
    cursor: pointer;
}

.Fact:hover .fact-value {
    opacity: 0.7;
}

.Fact:hover i {
    color: #dc3545;
}

.Fact .fas {
    color: #dc3545;
}

.Category .category-card {
    height: 100%;
    min-height: 40vh;
    border: unset;
}

.Category .overlay {
    position: unset;
}

.Category .search i:hover {
    cursor: pointer;
    opacity: 0.7;
}

.Category .category-title {
    text-shadow: 2px 2px 4px #000;
    font-size: 1.75rem;
    letter-spacing: 1.5px;
}

.Category .search-icon {
    vertical-align: middle;
    text-shadow: 2px 2px 4px #28a745;
}

.Category .loading-icon {
    vertical-align: middle;
    text-shadow: 2px 2px 4px #dc3545;
}

.FavouritesTable .show-me {
    opacity: 0;
    -webkit-transition: opacity 200ms ease;
    transition: opacity 200ms ease;
}

.FavouritesTable .stacked-icons:hover {
    cursor: pointer;
}

.FavouritesTable .stacked-icons:hover .show-me {
    opacity: 1;
    -webkit-transition: opacity 200ms ease;
    transition: opacity 200ms ease;
}

.FavouritesTable .category-toggle {
    /*opacity: 0.5;*/
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
}

.FavouritesTable .category-toggle:hover {
    cursor: pointer;
}

.FavouritesTable .show-facts {
    /*display: unset;*/
}

.FavouritesTable .hide-facts {
    display: none;
}

